











import ut from '@/utils/index'
export default {
	props: [],
	data() {
		return {
			classlist: [],
			active: 0
		}
	},
	methods: {
		req_class(){
			ut.request({
				data: {
					parentid:0
				},
				url: "goods/class"
			}).then(data=>{
				console.log(data)
				this.classlist=data;
				if(data&&data[0])this.cg_active(data[0],0)
			})
		},
		cg_active(list,index){
			this.active = index;
			ut.settitle(list.name);
			this.$emit('req_class',list.id)
		}
	},
	onLoad() {
		this.req_class()
	}
}
